var About=function About(){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("div",{className:"About__wrapper"},__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("article",{className:"About"},__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("section",{className:"who"},__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("h2",null,"Who"),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"We are a pair of lunatics who lack the social awareness and cultural couth to know where we belong in the world. Pretty offensive, we know. We met on the first day of college. Ugh, typical liberal, transcendentalist yahoos, we know. We graduated, took some jobs, and found out about work. That it\u2019s important, and that we\u2019re going to be doing it for a long time. We want to take some agency over our work and try our hands at something creative, unique, challenging, and, ultimately, something dope. So we\u2019re strapping a bunch of stuff to our bicycles and going for it. \u201CBut aren\u2019t you guys gonna get sick of one another?\u201D Undoubtedly, yes. Yes we will. But that\u2019s why we\u2019re bringing crossword puzzles.")),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("section",{className:"what"},__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("h2",null,"What"),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"Two wheels and human power is the theme of this (approximately) 15,000 mi-long trip, which is great because the ratio of kilocalories of energy input to kilometers of distance covered cycling is super efficient. Actually more so than travel by motorcycle, automobile, or train. And way more so than swimming. Could you imagine swimming to South America?! No thanks. Plus, we love the feeling of independence that you feel on the seat of your bike. Knowing that you\u2019ve got everything you need strapped to this rocket between your thighs, and that you, and you alone, are in control of the joystick. Tough feeling to beat. We recommend it to everyone.")),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("section",{className:"where"},__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("h2",null,"Where"),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"Well when it comes to independence, there simply is no other landmass that craves self-determination and sovereignty like the Americas. By our calculations, every single country in North and South America, with the exception of Canada, has run the gauntlet of national revolution for independence. We respect that. And we love the language and also eating empanadas. All of these critical factors played an important role in our decision making, but it is really the topography of the Americas that yanked our imaginations the most violently. Sun-scorched deserts, thousands of miles of coastline, tropical jungles, towering volcanoes and the longest mountain range on the planet. We want to see as much of it as we can.")),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("section",{className:"why"},__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("h2",null,"Why"),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("p",null,"Our purpose is to abandon routine and slow ourselves down. Adapting to our surroundings and embracing our challenges; forsaking acceleration in favor of presence. There is never a perfect time for any enormous endeavor\u2014but, dammit, we want to do something big while our bodies are young and our minds are uncorrupted by decades of cynicism...so by that measure, maybe this is OUR perfect time. An author that we both enjoy once wrote \u201CThe joy of life comes from our encounters with new experiences, and hence there is no greater joy than to have an endlessly changing horizon, for each day to have a new and different sun.\u201D Although Jon Krakauer did not personally ask us to test his hypothesis, we feel it is our duty to diligently conduct this experiment and uncover the truth\u2026or at least a vague estimation of it."))));};/* harmony default export */ __webpack_exports__["a"] = (About);

